.spotlight {
  height: 50vh;
  padding: 10px 30px;
  background: linear-gradient(to right bottom, #000428, blueviolet, blueviolet)
    no-repeat center center / cover;
  display: flex;
  justify-content: space-around;
  align-items: center;
  align-content: center;
  align-self: center;
}

.switch {
  font-family: "Grandstander", cursive;
}

.leftside h1 {
  font-size: 25px;
  font-family: Arvo, Serif;
  color: white;
  transition: transfrom 1s ease-in-out;
}

h2 {
  font-size: 1rem;
  color: white;
  font-family: "Grandstander", cursive;
  margin: 5px;
}

p {
  font-size: 0.5rem;
  margin: 5px;
  color: white;
  font-family: "Grandstander", cursive;
}

.flex {
  width: 200px;
  font-size: 28px;
  color: white;
  display: flex;
  justify-content: space-around;
  margin: 10px;
}

@media screen and (min-width: 500px) {
  .spotlight {
    height: 100vh;
    background: linear-gradient(
      to right bottom,
      #000428,
      blueviolet,
      blueviolet
    );

    display: flex;
    justify-content: space-around;
    align-items: center;
    align-content: center;
    align-self: center;
  }

  .switch {
    font-family: "Grandstander", cursive;
  }

  .leftside h1 {
    font-size: 55px;
    font-family: Arvo, Serif;
    color: white;
    transition: transfrom 1s ease-in-out;
  }

  h2 {
    font-size: 2rem;
    color: white;
    font-family: "Grandstander", cursive;
  }

  p {
    font-size: 1rem;
    color: white;
    font-family: "Grandstander", cursive;
  }

  .flex {
    width: 200px;
    font-size: 28px;
    color: white;
    display: flex;
    justify-content: space-around;
    margin: 10px;
  }
}
