.footer {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  align-content: center;
  align-self: center;
  padding: 15px 12px;
  background: linear-gradient(to right bottom, #000428, blueviolet, blueviolet)
    no-repeat center center / cover;
  font-family: "Grandstander", cursive;
  color: white;
  box-shadow: 3px -6px 8px 5px rgba(0, 0, 0, 0.3);
  z-index: 100;
}

.footer-header {
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: center;
  align-content: center;
  align-self: center;
  padding: 15px 12px;
  font-family: "Grandstander", cursive;
  color: white;
  z-index: 100;
  margin: 10px auto;
  padding: 20px auto;
}

.footer-header h1 {
  text-align: center;
}
.footer-body {
  margin: 10px auto;
}

.add div {
  display: flex;
  gap: 5px;
  justify-content: left;
  text-align: center;
  align-content: center;
  align-self: center;
  align-items: center;
}

.add div {
  margin: 20px;
}

@media screen and (min-width: 768px) {
  .footer-header {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    align-content: center;
    align-self: center;
    padding: 15px 12px;
    font-family: "Grandstander", cursive;
    color: white;
    z-index: 100;
    margin: 10px auto;
  }

  .footer-header h1 {
    text-align: left;
  }

  .add div {
    width: 500px;
    display: grid;
    gap: 5px;
    grid-template-columns: 50px 450px;
    justify-content: left;
    align-items: center;
    align-content: center;
    align-self: center;
    text-align: left;
  }

  .add div {
    margin: 0px;
  }
}
