.service {
  text-align: center;
  align-items: center;
  align-content: center;
  align-self: center;
  padding: 25px 12px;
  font-family: Arvo, Serif;
  margin-top: 100px;
  color: black;
  text-align: center;
  font-size: 20px;
  display: block;
  margin: 50px auto;
}
.service-card {
  font-size: 18px;
  padding: 15px 12px;
  border-radius: 10px;
  margin: 5px 10px;
  text-align: center;
  transition: all 0.5s ease-in-out;
}

.service-card:hover {
  border: 3px solid #000428;
}

.flexi2 {
  display: grid;
  grid-template-columns: 1fr;
  gap: 5px;
  margin: 10px 10px;
  justify-content: center;
  align-items: center;
  align-content: center;
  align-self: center;
  flex-wrap: wrap;
}

.servicecard-header {
  font-size: 100px;
  text-align: center;
  border-radius: 10px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
  background: linear-gradient(#000428, blueviolet, blueviolet);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.servicecard-header img {
  width: 150px;
}
.servicecard-body h1 {
  background: linear-gradient(#000428, blueviolet, blueviolet);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-family: "Source Code Pro", monospace;
  padding: 5px;
  margin-top: 10px;
  font-size: 18px;
  margin: 5px;
  color: black;
}

.servicecard-body .p1 {
  padding: 5px;
  margin-top: 10px;
  font-size: 18px;
  font-family: Arvo, Serif;
  margin: 5px;
  color: black;
}

.servicecard-body .p2 {
  padding: 5px;
  margin-top: 10px;
  font-size: 18px;
  font-family: "Source Code Pro", monospace;
  margin: 5px;
  color: black;
}

.button {
  width: 200px;
  margin: 10px auto;
  padding: 10px 10px;
  font-family: "Source Code Pro", monospace;
  background: linear-gradient(to right bottom, #e1f7ff, #3d99e2);
  border: 1px solid #3d99e2;
  font-size: 18px;
  font-weight: bolder;
  border-radius: 30px;
  color: white;
}

@media screen and (min-width: 468px) {
  .service-card {
    width: 100%;
  }
}
@media screen and (min-width: 768px) {
  .flexi2 {
    grid-template-columns: 1fr 1fr;
    font-size: 25px;
    margin: 30px 5%;
  }
  .service {
    font-size: 23px;
  }

  .servicecard-body h1 {
    font-family: "Source Code Pro", monospace;
    font-size: 25px;
    font-family: "Grandstander", cursive;
    margin: 5px;
  }
}
