.banner {
  text-align: center;
  background: linear-gradient(to right bottom, #000428, blueviolet, blueviolet);
  align-items: center;
  align-content: center;
  align-self: center;
  padding: 50px 12px 50px 12px;
  font-family: Arvo, Serif;
  color: white;
  text-align: center;
  font-size: 20px;
  display: block;
  margin-top: 10px;
}
.cards {
  width: 250px;
  font-size: 18px;
  padding: 15px 12px;
  border-radius: 10px;
  border: 2px solid white;
  margin: 5px 20px;
  text-align: center;
  box-shadow: 3px -6px 8px 5px rgba(0, 0, 0, 0.3);
}

.flexi {
  display: grid;
  grid-template-columns: 1fr;

  gap: 10px;
  margin: 10px 10%;
  justify-content: space-around;
  align-items: center;
  align-content: center;
  align-self: center;
  flex-wrap: wrap;
}

.cards-head {
  font-size: 100px;
  text-align: center;
  border-radius: 10px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}

.cards-body h1 {
  font-family: "Source Code Pro", monospace;
  padding: 5px;
  margin-top: 10px;
  font-size: 18px;
  font-family: "Grandstander", cursive;
  margin: 5px;
  box-shadow: 3px -6px 8px 5px rgba(0, 0, 0, 0.3);
}

.exps {
  font-family: "Source Code Pro", monospace;
  padding: 5px;
  gap: 10px;
  display: grid;
  margin-top: 10px;
  grid-template-columns: 1fr 1fr;
  font-size: 16px;
  font-weight: lighter;
  justify-content: space-around;
  align-items: center;
  align-content: center;
  align-self: center;
  flex-wrap: wrap;
  box-shadow: 3px -6px 8px 5px rgba(0, 0, 0, 0.3);
}

.exps span {
  font-size: 13px;
  margin: 5px;
}

@media screen and (min-width: 468px) {
  .flexi {
    grid-template-columns: 1fr 1fr;
    font-size: 25px;
  }
}

@media screen and (min-width: 888px) {
  .flexi {
    grid-template-columns: 1fr 1fr 1fr;
    font-size: 25px;
  }
}
@media screen and (min-width: 1140px) {
  .flexi {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    font-size: 25px;
  }
  .banner {
    font-size: 23px;
  }

  .cards-body h1 {
    font-family: "Source Code Pro", monospace;
    font-size: 20px;
    font-family: "Grandstander", cursive;
    margin: 5px;
  }
}
