.contact {
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  align-self: center;
  margin-top: 100px;
  gap: 20px;
  font-family: Arvo, serif;
}
label {
  margin: 5px;
  font-family: "Source Code Pro", monospace;
  font-weight: bold;
}
input,
textarea {
  width: 100%;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 10px;
  border: 1px solid seagreen;
  font-family: "Grandstander", cursive;
}

.contact div form {
  border: 3px solid seagreen;
  padding: 5px 20px;
  margin: 20px 10px;
  border-radius: 10px;
}

.contact div form div {
  padding: 5px 20px;
  margin: 20px 10px;
  border-radius: 10px;
}
.contact div form div {
  display: grid;
  gap: 30px;
  grid-template-columns: 1fr 2fr;
  justify-content: space-around;
  align-items: center;
  align-content: center;
  align-self: center;
}

#center {
  display: grid;
  gap: 10px;
  grid-template-columns: 1fr;
  justify-content: space-around;
  align-items: center;
  align-content: center;
  align-self: center;
}

.button {
  border: 1px solid seagreen;
}
