@import url("https://fonts.googleapis.com/css2?family=Source+Code+Pro&display=swap");
.aboutme {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  align-content: center;
  align-self: center;
  padding: 20px 20px;
  color: black;
}

.aboutme p {
  text-align: center;
  display: block;
  margin: 10px auto;
  color: black;
  font-size: 20px;
  font-family: "Source Code Pro", monospace;
}

.aboutme h1 {
  text-align: center;
  font-size: 25px;
  display: block;
  margin: 10px auto;
  color: black;

  font-family: Arvo, Serif;
}

.aboutme img {
  width: 200px;
  height: 200px;

  border-radius: 50%;
}

.profile {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-self: center;
  align-content: center;
  align-items: center;
}
.profile p {
  margin: 30px auto;
  color: black;
  font-size: 25px;
  font-family: "Source Code Pro", monospace;
}

.profile h1 {
  font-size: 25px;
  text-align: center;
  margin: 30px auto;
  font-family: Arvo, Serif;
}

.jobs {
  display: grid;
  border-radius: 25px;
  grid-template-columns: 1fr;
  flex-wrap: wrap;
  gap: 20px;
  margin: 10px;
  padding: 10px;
  border: 1px solid springgreen;
}

.jobs div h1 {
  text-align: left;
  font-size: 20px;
  font-family: Arvo, Serif;
}

.jobs div span {
  text-align: left;
  font-family: Arvo, Serif;
}

.jobs div h2 {
  font-size: 18px;
  color: black;
  text-align: left;
  text-transform: uppercase;
}

.jobs div p {
  font-size: 15px;
  color: black;
  text-align: left;
}

.jobs div ul {
  margin: 10px;
  font-size: 18px;
  color: black;
  text-align: left;
  font-family: "Source Code Pro", monospace;
}
.jobs div ul li {
  margin: 5px 25px;
  font-size: 15px;
  color: black;
  text-align: left;
  font-family: "Source Code Pro", monospace;
}
@media screen and (min-width: 768px) {
  .aboutme {
    padding: 30px 100px;
    color: black;
  }

  .aboutme h1 {
    font-size: 45px;
    margin: 30px auto;
  }

  .profile {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-self: center;
    align-content: center;
    align-items: center;
  }
  .profile p {
    margin: 30px auto;
    color: black;
    font-size: 25px;
    font-family: "Source Code Pro", monospace;
  }

  .profile h1 {
    font-size: 45px;
    margin: 30px auto;
  }

  .jobs {
    display: grid;
    border-radius: 25px;
    grid-template-columns: 1fr 3fr;
    gap: 20px;
    margin: 20px 5%;
    border: 1px solid springgreen;
  }

  .jobs div h1 {
    text-align: left;
    font-size: 25px;
    font-family: Arvo, Serif;
  }

  .jobs div span {
    text-align: left;
    font-family: Arvo, Serif;
  }

  .jobs div h2 {
    font-size: 25px;
    color: black;
    text-align: left;
    text-transform: uppercase;
  }

  .jobs div p {
    font-size: 18px;
    color: black;
    text-align: left;
  }

  .jobs div ul {
    margin: 10px;
    font-size: 18px;
    color: black;
    text-align: left;
    font-family: "Source Code Pro", monospace;
  }
  .jobs div ul li {
    margin: 5px 25px;
    font-size: 18px;
    color: black;
    text-align: left;
    font-family: "Source Code Pro", monospace;
  }
}
