.navbar {
  width: 100%;
  position: fixed;
  display: flex;
  justify-content: space-around;
  align-items: center;
  align-content: center;
  align-self: center;
  padding: 15px 12px;
  background: linear-gradient(to right bottom, #000428, blueviolet, blueviolet)
    no-repeat center center / cover;
  font-family: "Grandstander", cursive;
  color: white;
  box-shadow: 3px -6px 8px 5px rgba(0, 0, 0, 0.3);
  z-index: 100;
  top: 0;
}

.header h2 {
  z-index: 200;
  text-shadow: 3px -6px 8px 5px rgba(0, 0, 0, 0.3);
}

.menu-desk {
  display: none;
}
.menu {
  position: absolute;
  height: 70vh;
  width: 100%;
  background: linear-gradient(to right bottom, #000428, blueviolet, blueviolet)
    no-repeat center center / cover;
  top: 0;
  margin-top: 50px;
  text-decoration: none;
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-content: center;
  align-self: center;
  z-index: 1;
  clip-path: circle(0px at 90% -10%);
  -webkit-clip-path: circle(0px at 90% -10%);
  transition: all 0.2s ease-in-out;
}

.links {
  text-decoration: none;
  list-style: none;
  margin: 20px 15px;
  font-weight: bold;
  font-size: 16px;
  color: white;
  font-family: "Grandstander", cursive;
  text-transform: uppercase;
}
.open {
  cursor: pointer;
}
.open .line1,
.line2,
.line3 {
  width: 30px;
  height: 5px;
  background-color: white;
  border-radius: 2.5px;
}

.line1 {
  transition: all 0.1s ease-in-out;
  transform: rotate(0deg);
}
.line2 {
  margin-top: 4px;
  transition: all 0.1s ease-in-out;
  transform: rotate(0deg);
}

.line3 {
  margin-top: 4px;
  transition: all 0.1s ease-in-out;
  transform: rotate(0deg);
}

.toggle .line1 {
  transform: translateY(6px) rotate(135deg);
}

.toggle .line2 {
  opacity: 0;
  left: -60px;
}

.toggle .line3 {
  transform: translateY(-12px) rotate(-135deg);
}

.nav-active {
  clip-path: circle(1000px at 90% -10%);
  -webkit-clip-path: circle(1000px at 90% -10%);
}

@media screen and (min-width: 968px) {
  .open {
    display: none;
  }

  .navbar {
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    align-content: center;
    align-self: center;
    box-shadow: 3px -6px 8px 5px rgba(0, 0, 0, 0.3);
    z-index: 100;
  }

  .links {
    text-decoration: none;
    list-style: none;
    display: inline;
    margin: 30px 15px;
    font-weight: bold;
    font-size: 16px;
    color: white;
    font-family: "Grandstander", cursive;
    text-transform: uppercase;
  }

  .menu-desk {
    display: block;
  }

  .desk {
    display: flex;
    justify-content: space-around;
    align-items: center;
    align-content: center;
    align-self: center;
  }
}
